import React, { Component } from 'react';

export default class Square extends Component {
    render() {
        let squareClass = "square";
        if (this.props.isCurrent){
            squareClass += " square-current"
        }
        if(this.props.isInRow){
            squareClass += " square-in-row"
        }
        if(this.props.letter === "CONTROL"){
            squareClass = "square square-black"
        }
        return (
                <div className={squareClass} style={{width: '25px', height:'25px'}}>
                    {this.props.letter !== "CONTROL" ? this.props.letter : ""}
                </div>
            )
    }
}
