import React, { Component } from 'react'
import Square from "./Square"
import "./CrosswordComponents.css"

export default class Board extends Component {
    constructor(props){
        super(props);
        this.state = {
            board: this.makeBoard(this.props.width,this.props.height," "),
            userX: 0,
            userY: 0,
            isHorizontal: true
        }
    };
 
    makeBoard = (w, h, val) => {
        var arr = [];
        for(let i = 0; i < h; i++) {
            arr[i] = [];
            for(let j = 0; j < w; j++) {
                arr[i][j] = val;
            }
        }
        return arr;
    }
    
    setBoard = (x,y,letter) => {
        let newBoard = this.state.board; 
        newBoard[y][x] = letter.toUpperCase();
        this.setState({"board":newBoard});
    }

    moveUser = (xInc,yInc) =>{
        const {userX} = this.state;
        const {userY} = this.state;
        if(xInc === 1 && yInc === 0){
            if(userX !== this.props.width - 1 ){
                this.setState({"userX":userX + 1})
            }
        } else if(xInc === -1 && yInc === 0){
            if(userX !== 0){
                this.setState({"userX":userX - 1})
            }
        } else if(xInc === 0 && yInc === 1){
            if(userY !== 0){
                this.setState({"userY":userY - 1})
            }
        } else if(xInc === 0 && yInc === -1){
            if(userY !== this.props.height - 1){
                this.setState({"userY":userY + 1}) 
            }
        }
    }
    componentDidMount() {
        window.addEventListener("keydown",this.handleKeyDown);    
    }

    componentWillUnmount() {
        window.removeEventListener("keydown",this.handleKeyDown);
    }
    
    handleKeyDown = e => {
        const {key} = e;
        const {userX} = this.state;
        const {userY} = this.state;
        
        switch(key){
            case "ArrowLeft":
                this.moveUser(-1,0)
                break;
            case "ArrowRight":
                this.moveUser(1,0)
                break;
            case "ArrowUp":
                this.moveUser(0,1)
                break;
            case "ArrowDown":
                this.moveUser(0,-1)
                break;
            case "Enter":
                this.setState({"isHorizontal":!this.state.isHorizontal})
                break;
            case "Shift":
                // As of now do nothing
                break;
            case "Backspace":
                this.setBoard(userX,userY,"")
                if(this.state.isHorizontal){
                    this.moveUser(-1,0)
                }else {
                    this.moveUser(0,1)
                }
                break;
            case "Meta":
                break;
            default:
                this.setBoard(userX,userY,key)
                if(this.state.isHorizontal){
                    this.moveUser(1,0)
                }else {
                    this.moveUser(0,-1)
                }
            }
    }

    render() {
        return (
            <div className="board">
                {this.state.board.map((row,yIndex) => 
                {
                    return <div className="row" key={yIndex}>
                    {row.map((letter,xIndex) => 
                        {
                            let {userX} = this.state
                            let {userY} = this.state
                            let isInRow = false;
                            let isCurrent = (userX === xIndex && 
                                userY === yIndex) ? true : false;
                            if(this.state.isHorizontal){
                                if(xIndex !== userX && yIndex === userY){
                                    isInRow = true;
                                }
                            } else {
                                if(xIndex === userX && yIndex !== userY){
                                    isInRow = true;
                                }
                            }
                            return <Square letter={letter} isCurrent={isCurrent} isInRow={isInRow} key={xIndex}/>})
                        }
                    </div>
                    
                })}
            </div>
        )
    }
}
